import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Login from "./pages/Login";
import Home from "./pages/Home";
import Classes from "./pages/Classes";
import Student from "./pages/Users";
import Schedule from "./pages/Schedule";
import TeacherList from "./pages/TeacherList";
import StudentList from "./pages/StudentList";
import ResetPassword from "./pages/ResetPassword";
import ClassRosters from "./pages/ClassRosters";
import AddUserPage from "./pages/AddUserPage";
import ActivationForm from "./pages/ActivationForm";
import ProtectedRoute from "./components/ProtectedRoute";
import { AuthProvider } from "./context/AuthContext";
import './App.css';

import MySchedule from "./pages/MySchedule";
import MasterSchedule from "./pages/MasterSchedule";
import Schedules from "./pages/Schedules";
import CreateSchedule from "./pages/CreateSchedule";
import TeacherAvailability from "./pages/TeacherAvailability";


function App() {
  return (
    <AuthProvider>
      <Router>
        <Routes>
          <Route path="/" element={<Login />} />
          <Route
            path="/home"
            element={
              <ProtectedRoute>
                <Home />
              </ProtectedRoute>
            }
          />
          <Route
            path="/class"
            element={
              <ProtectedRoute>
                <Classes />
              </ProtectedRoute>
            }
          />
          <Route
            path="/student"
            element={
              <ProtectedRoute>
                <Student />
              </ProtectedRoute>
            }
          />
          <Route
            path="/schedule"
            element={
              <ProtectedRoute>
                <Schedule />
              </ProtectedRoute>
            }
          />
          <Route
            path="/teacher-list"
            element={
              <ProtectedRoute>
                <TeacherList />
              </ProtectedRoute>
            }
          />
          <Route
            path="/student-list"
            element={
              <ProtectedRoute>
                <StudentList />
              </ProtectedRoute>
            }
          />
          <Route
            path="/reset-password"
            element={<ResetPassword />}
          />
          <Route
            path="/rosters/:classId"
            element={
              <ProtectedRoute>
                <ClassRosters />
              </ProtectedRoute>
            }
          />
          <Route
            path="/add-user"
            element={
              <ProtectedRoute>
                <AddUserPage />
              </ProtectedRoute>
            }
          />
          <Route
            path="/activation-form"
            element={<ActivationForm />}
          />
          <Route
            path="/my-schedule"
            element={
              <ProtectedRoute>
                <MySchedule />
              </ProtectedRoute>
            }
          />
          <Route path="/schedules" element={<Schedules />} />
          <Route path="/create-schedule" element={<ProtectedRoute><CreateSchedule /></ProtectedRoute>} />
          <Route path="/master-schedule" element={<MasterSchedule />} />
          <Route path="/availability" element={<TeacherAvailability />} />
        </Routes>
      </Router>
    </AuthProvider>
  );
}

export default App;